import { useQuery, useMutation, useQueryClient } from "@tanstack/react-query";
import { supabase } from "@/integrations/supabase/client";
import { toast } from "sonner";
import { useTranslation } from "react-i18next";

export interface NavigationSettings {
  id: string;
  // Desktop settings (default/original fields)
  background_opacity: number;
  background_color: string;
  text_color: string;
  hover_bg_color: string;
  hover_text_color: string;
  shadow_enabled: boolean;
  // Tablet settings
  background_opacity_tablet: number | null;
  background_color_tablet: string | null;
  text_color_tablet: string | null;
  hover_bg_color_tablet: string | null;
  hover_text_color_tablet: string | null;
  shadow_enabled_tablet: boolean | null;
  // Mobile settings
  background_opacity_mobile: number | null;
  background_color_mobile: string | null;
  text_color_mobile: string | null;
  hover_bg_color_mobile: string | null;
  hover_text_color_mobile: string | null;
  shadow_enabled_mobile: boolean | null;
  created_at: string;
  updated_at: string;
}

export interface NavigationButton {
  id: string;
  label_zh: string;
  label_en: string;
  label_pt: string;
  link: string;
  icon: string;
  sort_order: number;
  visible: boolean;
  show_on_desktop: boolean;
  show_on_tablet: boolean;
  show_on_mobile: boolean;
  show_when_logged_in: boolean;
  show_when_logged_out: boolean;
  created_at: string;
  updated_at: string;
}

// Helper to get device-specific settings with fallback to desktop
export const getDeviceSettings = (
  settings: NavigationSettings | null | undefined,
  device: 'desktop' | 'tablet' | 'mobile'
) => {
  if (!settings) {
    return {
      background_opacity: 0,
      background_color: "transparent",
      text_color: "primary",
      hover_bg_color: "black",
      hover_text_color: "white",
      shadow_enabled: true,
    };
  }

  if (device === 'desktop') {
    return {
      background_opacity: settings.background_opacity ?? 0,
      background_color: settings.background_color || "transparent",
      text_color: settings.text_color || "primary",
      hover_bg_color: settings.hover_bg_color || "black",
      hover_text_color: settings.hover_text_color || "white",
      shadow_enabled: settings.shadow_enabled ?? true,
    };
  }

  if (device === 'tablet') {
    return {
      background_opacity: settings.background_opacity_tablet ?? settings.background_opacity ?? 0,
      background_color: settings.background_color_tablet || settings.background_color || "transparent",
      text_color: settings.text_color_tablet || settings.text_color || "primary",
      hover_bg_color: settings.hover_bg_color_tablet || settings.hover_bg_color || "black",
      hover_text_color: settings.hover_text_color_tablet || settings.hover_text_color || "white",
      shadow_enabled: settings.shadow_enabled_tablet ?? settings.shadow_enabled ?? true,
    };
  }

  // mobile
  return {
    background_opacity: settings.background_opacity_mobile ?? settings.background_opacity ?? 0,
    background_color: settings.background_color_mobile || settings.background_color || "transparent",
    text_color: settings.text_color_mobile || settings.text_color || "primary",
    hover_bg_color: settings.hover_bg_color_mobile || settings.hover_bg_color || "black",
    hover_text_color: settings.hover_text_color_mobile || settings.hover_text_color || "white",
    shadow_enabled: settings.shadow_enabled_mobile ?? settings.shadow_enabled ?? true,
  };
};

export const useNavigationSettings = () => {
  const { t } = useTranslation();
  const queryClient = useQueryClient();

  const { data: settings, isLoading: isLoadingSettings } = useQuery({
    queryKey: ["navigation-settings"],
    queryFn: async () => {
      const { data, error } = await supabase
        .from("navigation_settings")
        .select("*")
        .single();

      if (error) throw error;
      return data as NavigationSettings;
    },
  });

  const { data: buttons, isLoading: isLoadingButtons } = useQuery({
    queryKey: ["navigation-buttons"],
    queryFn: async () => {
      const { data, error } = await supabase
        .from("navigation_buttons")
        .select("*")
        .order("sort_order", { ascending: true });

      if (error) throw error;
      return data as NavigationButton[];
    },
  });

  const updateSettings = useMutation({
    mutationFn: async (updates: Partial<NavigationSettings>) => {
      if (!settings?.id) throw new Error("No settings found");

      const { data, error } = await supabase
        .from("navigation_settings")
        .update({
          ...updates,
          updated_at: new Date().toISOString(),
        })
        .eq("id", settings.id)
        .select()
        .single();

      if (error) throw error;
      return data;
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["navigation-settings"] });
      toast.success(t("admin.settingsSaved"));
    },
    onError: (error) => {
      console.error("Error updating navigation settings:", error);
      toast.error(t("error.general"));
    },
  });

  const createButton = useMutation({
    mutationFn: async (button: Omit<NavigationButton, "id" | "created_at" | "updated_at">) => {
      const { data, error } = await supabase
        .from("navigation_buttons")
        .insert(button)
        .select()
        .single();

      if (error) throw error;
      return data;
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["navigation-buttons"] });
      toast.success(t("admin.settingsSaved"));
    },
    onError: (error) => {
      console.error("Error creating navigation button:", error);
      toast.error(t("error.general"));
    },
  });

  const updateButton = useMutation({
    mutationFn: async ({ id, ...updates }: Partial<NavigationButton> & { id: string }) => {
      const { data, error } = await supabase
        .from("navigation_buttons")
        .update({
          ...updates,
          updated_at: new Date().toISOString(),
        })
        .eq("id", id)
        .select()
        .single();

      if (error) throw error;
      return data;
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["navigation-buttons"] });
      toast.success(t("admin.settingsSaved"));
    },
    onError: (error) => {
      console.error("Error updating navigation button:", error);
      toast.error(t("error.general"));
    },
  });

  const deleteButton = useMutation({
    mutationFn: async (id: string) => {
      const { error } = await supabase
        .from("navigation_buttons")
        .delete()
        .eq("id", id);

      if (error) throw error;
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["navigation-buttons"] });
      toast.success(t("admin.settingsSaved"));
    },
    onError: (error) => {
      console.error("Error deleting navigation button:", error);
      toast.error(t("error.general"));
    },
  });

  const reorderButtons = useMutation({
    mutationFn: async (orderedIds: string[]) => {
      const updates = orderedIds.map((id, index) => ({
        id,
        sort_order: index + 1,
      }));

      for (const update of updates) {
        const { error } = await supabase
          .from("navigation_buttons")
          .update({ sort_order: update.sort_order })
          .eq("id", update.id);

        if (error) throw error;
      }
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["navigation-buttons"] });
    },
    onError: (error) => {
      console.error("Error reordering buttons:", error);
      toast.error(t("error.general"));
    },
  });

  return {
    settings,
    buttons,
    isLoading: isLoadingSettings || isLoadingButtons,
    updateSettings,
    createButton,
    updateButton,
    deleteButton,
    reorderButtons,
  };
};
