import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog";
import { ShieldX, Mail, Phone } from "lucide-react";
import { useTranslation } from "react-i18next";
import { useSiteSettings } from "@/hooks/useSiteSettings";

interface BannedUserDialogProps {
  open: boolean;
  banReason?: string;
  onConfirm: () => void;
}

const BannedUserDialog = ({ open, banReason, onConfirm }: BannedUserDialogProps) => {
  const { t } = useTranslation();
  const { settings } = useSiteSettings();

  return (
    <AlertDialog open={open}>
      <AlertDialogContent className="max-w-md">
        <AlertDialogHeader className="space-y-4">
          <div className="mx-auto flex h-16 w-16 items-center justify-center rounded-full bg-destructive/10">
            <ShieldX className="h-8 w-8 text-destructive" />
          </div>
          <AlertDialogTitle className="text-center text-xl">
            {t("auth.bannedDialog.title")}
          </AlertDialogTitle>
          <AlertDialogDescription className="text-center space-y-4">
            <p className="text-base">
              {banReason 
                ? t("auth.bannedDialog.reasonMessage", { reason: banReason })
                : t("auth.bannedDialog.defaultMessage")
              }
            </p>
            <div className="rounded-lg bg-muted p-4 text-left space-y-2">
              <p className="text-sm font-medium text-foreground">
                {t("auth.bannedDialog.needHelp")}
              </p>
              {settings?.contact_email && (
                <div className="flex items-center gap-2 text-sm text-muted-foreground">
                  <Mail className="h-4 w-4" />
                  <span>{settings.contact_email}</span>
                </div>
              )}
              {settings?.contact_phone && (
                <div className="flex items-center gap-2 text-sm text-muted-foreground">
                  <Phone className="h-4 w-4" />
                  <span>{settings.contact_phone}</span>
                </div>
              )}
            </div>
          </AlertDialogDescription>
        </AlertDialogHeader>
        <AlertDialogFooter className="mt-4">
          <AlertDialogAction onClick={onConfirm} className="w-full">
            {t("auth.bannedDialog.confirm")}
          </AlertDialogAction>
        </AlertDialogFooter>
      </AlertDialogContent>
    </AlertDialog>
  );
};

export default BannedUserDialog;
