import { useState } from "react";
import { useTranslation } from "react-i18next";
import { MessageSquare, Send, Loader2, ImagePlus, X } from "lucide-react";
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle } from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { toast } from "sonner";
import { supabase } from "@/integrations/supabase/client";

interface FeedbackDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
}

const FeedbackDialog = ({ open, onOpenChange }: FeedbackDialogProps) => {
  const { t } = useTranslation();
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [email, setEmail] = useState("");
  const [reason, setReason] = useState("");
  const [content, setContent] = useState("");
  const [imageFile, setImageFile] = useState<File | null>(null);
  const [imagePreview, setImagePreview] = useState<string | null>(null);

  const feedbackReasons = [
    { value: "bug", label: t("feedback.reasons.bug", "Bug Report") },
    { value: "suggestion", label: t("feedback.reasons.suggestion", "Suggestion") },
    { value: "feature", label: t("feedback.reasons.feature", "Feature Request") },
    { value: "complaint", label: t("feedback.reasons.complaint", "Complaint") },
    { value: "praise", label: t("feedback.reasons.praise", "Praise") },
    { value: "other", label: t("feedback.reasons.other", "Other") },
  ];

  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      if (file.size > 5 * 1024 * 1024) {
        toast.error(t("feedback.imageTooLarge", "Image size must be less than 5MB"));
        return;
      }
      setImageFile(file);
      const reader = new FileReader();
      reader.onloadend = () => {
        setImagePreview(reader.result as string);
      };
      reader.readAsDataURL(file);
    }
  };

  const removeImage = () => {
    setImageFile(null);
    setImagePreview(null);
  };

  const uploadImage = async (): Promise<string | null> => {
    if (!imageFile) return null;

    const fileExt = imageFile.name.split(".").pop();
    const fileName = `${Date.now()}-${Math.random().toString(36).substring(7)}.${fileExt}`;
    const filePath = `${fileName}`;

    const { error: uploadError } = await supabase.storage
      .from("feedback-images")
      .upload(filePath, imageFile);

    if (uploadError) {
      console.error("Upload error:", uploadError);
      throw uploadError;
    }

    const { data: { publicUrl } } = supabase.storage
      .from("feedback-images")
      .getPublicUrl(filePath);

    return publicUrl;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!email || !reason || !content) {
      toast.error(t("feedback.fillAllFields", "Please fill in all fields"));
      return;
    }

    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(email)) {
      toast.error(t("feedback.invalidEmail", "Please enter a valid email address"));
      return;
    }

    setIsSubmitting(true);

    try {
      let imageUrl: string | null = null;
      if (imageFile) {
        imageUrl = await uploadImage();
      }

      const { error } = await supabase.functions.invoke("send-feedback-email", {
        body: { email, reason, content, image_url: imageUrl }
      });

      if (error) {
        throw error;
      }

      toast.success(t("feedback.submitSuccess", "Feedback submitted successfully! Thank you."));
      onOpenChange(false);
      setEmail("");
      setReason("");
      setContent("");
      setImageFile(null);
      setImagePreview(null);
    } catch (error: any) {
      console.error("Error submitting feedback:", error);
      toast.error(t("feedback.submitError", "Failed to submit feedback. Please try again."));
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="sm:max-w-md max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2">
            <MessageSquare className="h-5 w-5" />
            {t("feedback.title", "Feedback")}
          </DialogTitle>
          <DialogDescription>
            {t("feedback.description", "We value your feedback! Let us know if you encounter any issues or have suggestions.")}
          </DialogDescription>
        </DialogHeader>
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="feedback-email">{t("feedback.email", "Your Email")} *</Label>
            <Input
              id="feedback-email"
              type="email"
              placeholder={t("feedback.emailPlaceholder", "your@email.com")}
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              required
            />
          </div>
          <div className="space-y-2">
            <Label htmlFor="feedback-reason">{t("feedback.reason", "Feedback Type")} *</Label>
            <Select value={reason} onValueChange={setReason} required>
              <SelectTrigger id="feedback-reason">
                <SelectValue placeholder={t("feedback.selectReason", "Select feedback type")} />
              </SelectTrigger>
              <SelectContent>
                {feedbackReasons.map((r) => (
                  <SelectItem key={r.value} value={r.value}>
                    {r.label}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
          <div className="space-y-2">
            <Label htmlFor="feedback-content">{t("feedback.content", "Details")} *</Label>
            <Textarea
              id="feedback-content"
              placeholder={t("feedback.contentPlaceholder", "Please describe your feedback in detail...")}
              value={content}
              onChange={(e) => setContent(e.target.value)}
              rows={4}
              required
            />
          </div>
          
          {/* Image Upload */}
          <div className="space-y-2">
            <Label>{t("feedback.image", "Screenshot (Optional)")}</Label>
            {imagePreview ? (
              <div className="relative w-full">
                <img
                  src={imagePreview}
                  alt="Preview"
                  className="w-full h-32 object-cover rounded-md border"
                />
                <Button
                  type="button"
                  variant="destructive"
                  size="icon"
                  className="absolute top-2 right-2 h-6 w-6"
                  onClick={removeImage}
                >
                  <X className="h-4 w-4" />
                </Button>
              </div>
            ) : (
              <label className="flex flex-col items-center justify-center w-full h-24 border-2 border-dashed rounded-md cursor-pointer hover:bg-muted/50 transition-colors">
                <ImagePlus className="h-6 w-6 text-muted-foreground" />
                <span className="mt-1 text-sm text-muted-foreground">
                  {t("feedback.uploadImage", "Click to upload")}
                </span>
                <input
                  type="file"
                  accept="image/*"
                  className="hidden"
                  onChange={handleImageChange}
                />
              </label>
            )}
          </div>

          <div className="flex justify-end gap-2">
            <Button type="button" variant="outline" onClick={() => onOpenChange(false)}>
              {t("common.cancel")}
            </Button>
            <Button type="submit" disabled={isSubmitting}>
              {isSubmitting ? (
                <>
                  <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                  {t("common.submitting", "Submitting...")}
                </>
              ) : (
                <>
                  <Send className="mr-2 h-4 w-4" />
                  {t("common.submit", "Submit")}
                </>
              )}
            </Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
};

export default FeedbackDialog;
