import { useEffect, useRef } from 'react';
import { supabase } from '@/integrations/supabase/client';
import { useAuth } from '@/contexts/AuthContext';
import { isInWebView } from '@/lib/externalLinks';

const LOCATION_CACHE_KEY = "location_cache";

interface LocationData {
  country: string | null;
  city: string | null;
  ip: string | null;
}

// Get cached location data from sessionStorage
const getCachedLocation = (): LocationData | null => {
  try {
    const cached = sessionStorage.getItem(LOCATION_CACHE_KEY);
    if (cached) {
      const data = JSON.parse(cached);
      return {
        country: data.country || null,
        city: data.city || null,
        ip: data.ip || null,
      };
    }
  } catch {
    // Ignore parse errors
  }
  return null;
};

// Cache location data to sessionStorage
const cacheLocation = (data: LocationData): void => {
  try {
    sessionStorage.setItem(LOCATION_CACHE_KEY, JSON.stringify(data));
  } catch {
    // Ignore storage errors
  }
};

// Fetch location data via Edge Function (avoids CORS and rate limits)
const fetchLocationData = async (): Promise<LocationData> => {
  // Check cache first
  const cached = getCachedLocation();
  if (cached) {
    return cached;
  }

  try {
    const { data, error } = await supabase.functions.invoke('get-location');
    
    if (error) {
      console.error("Failed to fetch location:", error);
      return { country: null, city: null, ip: null };
    }

    const locationData: LocationData = {
      country: data?.country || null,
      city: data?.city || null,
      ip: data?.ip || null,
    };

    // Cache successful result
    if (locationData.country || locationData.city) {
      cacheLocation(locationData);
    }

    return locationData;
  } catch (error) {
    console.error("Failed to fetch location data:", error);
    return { country: null, city: null, ip: null };
  }
};

export const useUserPresence = () => {
  const { user } = useAuth();
  const channelRef = useRef<ReturnType<typeof supabase.channel> | null>(null);
  const locationDataRef = useRef<{ country: string | null; city: string | null; ip: string | null } | null>(null);
  const initDelayRef = useRef<NodeJS.Timeout | null>(null);

  useEffect(() => {
    if (!user?.id) return;

    console.log('Initializing user presence for:', user.id);

    const updatePresenceInDB = async (isOnline: boolean, includeLocation: boolean = false) => {
      try {
        // Check if user has valid session before updating
        const { data: { session } } = await supabase.auth.getSession();
        if (!session) {
          console.warn('No active session, skipping presence update');
          return;
        }

        const updateData: {
          user_id: string;
          is_online: boolean;
          last_seen: string;
          updated_at: string;
          ip_address?: string | null;
          city?: string | null;
          country?: string | null;
        } = {
          user_id: user.id,
          is_online: isOnline,
          last_seen: new Date().toISOString(),
          updated_at: new Date().toISOString()
        };

        if (includeLocation && locationDataRef.current) {
          updateData.ip_address = locationDataRef.current.ip;
          updateData.city = locationDataRef.current.city;
          updateData.country = locationDataRef.current.country;
        }

        const { error } = await supabase
          .from('user_presence')
          .upsert(updateData, {
            onConflict: 'user_id'
          });

        if (error) {
          console.error('Error updating presence in DB:', error);
        }
      } catch (err) {
        console.error('Error in updatePresenceInDB:', err);
      }
    };

    const initializePresence = async () => {
      // Fetch location data once
      const locationData = await fetchLocationData();
      locationDataRef.current = locationData;
      console.log('Location data fetched:', locationData);

      // Create presence channel with user.id as the presence key
      // This ensures each user is tracked under their own user_id
      const channel = supabase.channel('online-users', {
        config: {
          presence: {
            key: user.id, // Use user.id as the presence key
          },
        },
      });

      channel
        .on('presence', { event: 'sync' }, () => {
          const state = channel.presenceState();
          console.log('User presence sync, total online:', Object.keys(state).length);
        })
        .subscribe(async (status) => {
          console.log('User presence channel status:', status);
          if (status === 'SUBSCRIBED') {
            // Track user presence - data will be stored under the key (user.id)
            const trackResult = await channel.track({
              user_id: user.id,
              online_at: new Date().toISOString(),
            });
            console.log('User tracked:', user.id, 'Result:', trackResult);
            
            // Also update database record with location info
            await updatePresenceInDB(true, true);
          }
        });

      channelRef.current = channel;
    };

    // Always delay presence initialization to prioritize page rendering
    // Use consistent 2 second delay for stable behavior across all environments
    const initDelay = 2000;
    
    if (initDelay > 0) {
      console.log('Delaying presence initialization for WebView performance');
      initDelayRef.current = setTimeout(initializePresence, initDelay);
    } else {
      initializePresence();
    }

    // Heartbeat to update last_seen - use consistent 60 second interval
    const heartbeatInterval = 60000;
    const heartbeat = setInterval(() => {
      updatePresenceInDB(true, false);
      // Re-track presence to keep WebSocket alive
      if (channelRef.current) {
        channelRef.current.track({
          user_id: user.id,
          online_at: new Date().toISOString(),
        });
      }
    }, heartbeatInterval);

    // Handle visibility change
    const handleVisibilityChange = () => {
      if (document.visibilityState === 'visible') {
        // Re-track presence when tab becomes visible
        if (channelRef.current) {
          channelRef.current.track({
            user_id: user.id,
            online_at: new Date().toISOString(),
          });
        }
        updatePresenceInDB(true, false);
      }
    };

    // Handle beforeunload - set offline in DB
    const handleBeforeUnload = () => {
      updatePresenceInDB(false, false);
    };

    document.addEventListener('visibilitychange', handleVisibilityChange);
    window.addEventListener('beforeunload', handleBeforeUnload);

    return () => {
      console.log('Cleaning up user presence for:', user.id);
      
      // Clear init delay if still pending
      if (initDelayRef.current) {
        clearTimeout(initDelayRef.current);
      }
      
      clearInterval(heartbeat);
      document.removeEventListener('visibilitychange', handleVisibilityChange);
      window.removeEventListener('beforeunload', handleBeforeUnload);
      
      // Untrack and cleanup
      if (channelRef.current) {
        channelRef.current.untrack();
        supabase.removeChannel(channelRef.current);
      }
      
      updatePresenceInDB(false, false);
    };
  }, [user?.id]);
};
