import { useEffect, useRef } from "react";
import { useNavigate } from "react-router-dom";
import { isIOSDevice, isIOSWebView, isCapacitorApp } from "@/lib/externalLinks";

/**
 * iOS-only edge swipe back gesture hook
 * Only enables on iOS WebView or Capacitor iOS app to avoid conflicts with other platforms
 */
export const useIOSSwipeBack = () => {
  const navigate = useNavigate();
  const touchStartX = useRef<number>(0);
  const touchStartY = useRef<number>(0);
  const isSwiping = useRef<boolean>(false);
  
  useEffect(() => {
    // Only enable for iOS WebView or iOS Capacitor app
    const isIOSApp = isIOSDevice() && (isIOSWebView() || isCapacitorApp());
    if (!isIOSApp) return;
    
    // Check if Capacitor is iOS platform specifically
    const cap = (window as any).Capacitor;
    if (cap?.getPlatform && cap.getPlatform() === 'android') return;
    
    const EDGE_THRESHOLD = 30; // px from left edge to trigger
    const MIN_SWIPE_DISTANCE = 80; // minimum horizontal swipe distance
    const MAX_VERTICAL_RATIO = 0.5; // max vertical/horizontal ratio (to distinguish from scroll)
    
    const handleTouchStart = (e: TouchEvent) => {
      const touch = e.touches[0];
      // Only start tracking if touch begins near left edge
      if (touch.clientX <= EDGE_THRESHOLD) {
        touchStartX.current = touch.clientX;
        touchStartY.current = touch.clientY;
        isSwiping.current = true;
      } else {
        isSwiping.current = false;
      }
    };
    
    const handleTouchMove = (e: TouchEvent) => {
      // Optionally add visual feedback here in the future
    };
    
    const handleTouchEnd = (e: TouchEvent) => {
      if (!isSwiping.current) return;
      
      const touch = e.changedTouches[0];
      const deltaX = touch.clientX - touchStartX.current;
      const deltaY = Math.abs(touch.clientY - touchStartY.current);
      
      // Check if it's a valid horizontal swipe from left edge
      if (
        deltaX >= MIN_SWIPE_DISTANCE &&
        (deltaY / deltaX) <= MAX_VERTICAL_RATIO
      ) {
        // Navigate back
        navigate(-1);
      }
      
      isSwiping.current = false;
    };
    
    // Use passive listeners for better scroll performance
    document.addEventListener('touchstart', handleTouchStart, { passive: true });
    document.addEventListener('touchmove', handleTouchMove, { passive: true });
    document.addEventListener('touchend', handleTouchEnd, { passive: true });
    
    return () => {
      document.removeEventListener('touchstart', handleTouchStart);
      document.removeEventListener('touchmove', handleTouchMove);
      document.removeEventListener('touchend', handleTouchEnd);
    };
  }, [navigate]);
};
