import { useQuery, useMutation, useQueryClient } from "@tanstack/react-query";
import { supabase } from "@/integrations/supabase/client";
import { toast } from "sonner";
import { useTranslation } from "react-i18next";

export interface SiteSettings {
  id: string;
  logo_url: string | null;
  logo_width: number;
  logo_height: number;
  favicon_url: string | null;
  join_success_title_zh: string | null;
  join_success_title_en: string | null;
  join_success_title_pt: string | null;
  join_success_message_zh: string | null;
  join_success_message_en: string | null;
  join_success_message_pt: string | null;
  join_contact_phone: string | null;
  contact_phone: string | null;
  contact_email: string | null;
  created_at: string;
  updated_at: string;
}

export const useSiteSettings = () => {
  const { t } = useTranslation();
  const queryClient = useQueryClient();

  const { data: settings, isLoading } = useQuery({
    queryKey: ["site-settings"],
    queryFn: async () => {
      const { data, error } = await supabase
        .from("site_settings")
        .select("*")
        .single();

      if (error) throw error;
      return data as SiteSettings;
    },
  });

  const updateSettings = useMutation({
    mutationFn: async (updates: Partial<SiteSettings>) => {
      if (!settings?.id) throw new Error("No settings found");

      const { data, error } = await supabase
        .from("site_settings")
        .update({
          ...updates,
          updated_at: new Date().toISOString(),
        })
        .eq("id", settings.id)
        .select()
        .single();

      if (error) throw error;
      return data;
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["site-settings"] });
      toast.success(t("admin.settingsSaved"));
    },
    onError: (error) => {
      console.error("Error updating settings:", error);
      toast.error(t("error.general"));
    },
  });

  const uploadLogo = async (file: File): Promise<string | null> => {
    const fileExt = file.name.split(".").pop();
    const fileName = `logo-${Date.now()}.${fileExt}`;
    const filePath = `site/${fileName}`;

    const { error: uploadError } = await supabase.storage
      .from("site-assets")
      .upload(filePath, file);

    if (uploadError) {
      console.error("Upload error:", uploadError);
      toast.error(t("error.uploadFailed"));
      return null;
    }

    const { data } = supabase.storage
      .from("site-assets")
      .getPublicUrl(filePath);

    return data.publicUrl;
  };

  return {
    settings,
    isLoading,
    updateSettings,
    uploadLogo,
  };
};
