import { useTranslation } from "react-i18next";
import { Link } from "react-router-dom";
import { Phone, Mail, MessageSquare } from "lucide-react";
import { useSiteSettings } from "@/hooks/useSiteSettings";
import { VERSION } from "@/lib/version";
import { useState } from "react";
import FeedbackDialog from "@/components/FeedbackDialog";

const Footer = () => {
  const { t } = useTranslation();
  const { settings } = useSiteSettings();
  const [feedbackOpen, setFeedbackOpen] = useState(false);

  return (
    <footer className="border-t bg-card">
      <div className="container mx-auto px-4 py-8">
        <div className="grid gap-8 md:grid-cols-3">
          {/* Brand */}
          <div className="space-y-3">
            <h3 className="font-bold text-lg">{t("footer.brandName")}</h3>
            <p className="text-sm text-muted-foreground">
              {t("footer.description")}
            </p>
          </div>

          {/* Contact Info */}
          <div className="space-y-3">
            <h4 className="font-semibold">{t("footer.contact")}</h4>
            <div className="space-y-2 text-sm text-muted-foreground">
              {settings?.contact_phone && (
                <a 
                  href={`tel:${settings.contact_phone}`}
                  className="flex items-center gap-2 hover:text-foreground transition-colors"
                >
                  <Phone className="h-4 w-4" />
                  {settings.contact_phone}
                </a>
              )}
              {settings?.contact_email && (
                <a 
                  href={`mailto:${settings.contact_email}`}
                  className="flex items-center gap-2 hover:text-foreground transition-colors"
                >
                  <Mail className="h-4 w-4" />
                  {settings.contact_email}
                </a>
              )}
            </div>
          </div>

          {/* Links */}
          <div className="space-y-3">
            <h4 className="font-semibold">{t("footer.links")}</h4>
            <div className="space-y-2 text-sm">
              <Link to="/privacy-policy" className="block text-muted-foreground hover:text-foreground transition-colors">
                {t("footer.privacyPolicy")}
              </Link>
              <Link to="/terms-of-service" className="block text-muted-foreground hover:text-foreground transition-colors">
                {t("footer.termsOfService")}
              </Link>
              <Link to="/join-us" className="block text-muted-foreground hover:text-foreground transition-colors">
                {t("footer.joinUs")}
              </Link>
              <button 
                onClick={() => setFeedbackOpen(true)}
                className="inline-flex items-center gap-2 px-3 py-1.5 rounded-full bg-primary/10 text-primary hover:bg-primary/20 transition-colors text-sm font-medium"
              >
                <MessageSquare className="h-4 w-4" />
                {t("feedback.title")}
              </button>
            </div>
          </div>
        </div>

        <div className="mt-8 pt-6 border-t flex flex-col sm:flex-row items-center justify-between gap-2 text-sm text-muted-foreground">
          <span> {new Date().getFullYear()} {t("footer.brandName")}. {t("footer.allRightsReserved")}</span>
          <span>v{VERSION.app}</span>
        </div>
      </div>

      <FeedbackDialog open={feedbackOpen} onOpenChange={setFeedbackOpen} />
    </footer>
  );
};

export default Footer;
