import { Skeleton } from "@/components/ui/skeleton";

const HomeSkeleton = () => {
  return (
    <div className="min-h-screen bg-background animate-skeleton-fade-in">
      {/* Hero Section Skeleton */}
      <section className="relative min-h-screen flex items-center overflow-hidden">
        {/* Background gradient skeleton */}
        <div className="absolute inset-0 bg-gradient-to-b from-muted/50 via-muted/30 to-muted/50" />
        
        {/* Stats skeleton - top left */}
        <div className="absolute top-3 sm:top-4 left-3 z-20">
          <div className="gap-4 w-fit flex-col flex items-start justify-start">
            <div className="flex items-center gap-4">
              <Skeleton className="h-6 w-24" />
              <Skeleton className="h-6 w-24" />
            </div>
            <Skeleton className="h-8 w-40" />
          </div>
        </div>

        {/* Content skeleton */}
        <div className="relative z-10 container mx-auto px-4 sm:px-6 flex items-center min-h-screen">
          <div className="max-w-4xl mx-auto text-center space-y-4 sm:space-y-8 w-full">
            {/* Subtitle button skeleton */}
            <div className="flex justify-center">
              <Skeleton className="h-11 w-48 rounded-md" />
            </div>
            
            {/* Title skeleton */}
            <div className="space-y-3 px-4">
              <Skeleton className="h-12 sm:h-16 md:h-20 w-3/4 mx-auto" />
              <Skeleton className="h-12 sm:h-16 md:h-20 w-2/3 mx-auto" />
            </div>
            
            {/* Description skeleton */}
            <div className="space-y-2 px-4 max-w-2xl mx-auto">
              <Skeleton className="h-5 sm:h-6 w-full" />
              <Skeleton className="h-5 sm:h-6 w-4/5 mx-auto" />
            </div>
            
            {/* Buttons skeleton */}
            <div className="flex flex-col sm:flex-row gap-3 sm:gap-4 justify-center pt-2 sm:pt-4 px-4">
              <Skeleton className="h-11 w-full sm:w-44 rounded-md" />
              <Skeleton className="h-11 w-full sm:w-36 rounded-md" />
            </div>
          </div>
        </div>

        {/* Carousel dots skeleton */}
        <div className="absolute bottom-8 left-1/2 -translate-x-1/2 z-30 flex gap-2">
          <Skeleton className="w-8 h-3 rounded-full" />
          <Skeleton className="w-3 h-3 rounded-full" />
          <Skeleton className="w-3 h-3 rounded-full" />
        </div>
      </section>

      {/* Features Section Skeleton */}
      <section className="py-16 sm:py-24 px-4 bg-secondary/20">
        <div className="container mx-auto max-w-6xl">
          {/* Section header skeleton */}
          <div className="text-center mb-12 sm:mb-16">
            <Skeleton className="h-10 sm:h-12 w-64 mx-auto mb-4" />
            <Skeleton className="h-5 w-80 mx-auto" />
          </div>

          {/* Feature cards skeleton */}
          <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-6 sm:gap-8">
            {[1, 2, 3].map((i) => (
              <div key={i} className="p-6 sm:p-8 border border-border/50 rounded-lg bg-card">
                <Skeleton className="w-14 sm:w-16 h-14 sm:h-16 rounded-2xl mb-4 sm:mb-6" />
                <Skeleton className="h-7 w-32 mb-2 sm:mb-3" />
                <Skeleton className="h-4 w-full" />
                <Skeleton className="h-4 w-3/4 mt-2" />
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* CTA Section Skeleton */}
      <section className="py-16 sm:py-24 px-4">
        <div className="container mx-auto max-w-4xl text-center">
          <Skeleton className="h-10 sm:h-12 w-72 mx-auto mb-4" />
          <Skeleton className="h-5 w-96 max-w-full mx-auto mb-8" />
          <Skeleton className="h-11 w-40 mx-auto rounded-md" />
        </div>
      </section>
    </div>
  );
};

export default HomeSkeleton;
