import { useTranslation } from "react-i18next";
import { Button } from "@/components/ui/button";
import { DropdownMenu, DropdownMenuContent, DropdownMenuItem, DropdownMenuTrigger } from "@/components/ui/dropdown-menu";
import { Globe } from "lucide-react";
import { useUpdateLanguagePreference } from "@/hooks/useProfile";
import { useAuth } from "@/contexts/AuthContext";
const languages = [{
  code: "en",
  name: "English",
  flag: ""
}, {
  code: "zh",
  name: "",
  flag: ""
}, {
  code: "pt",
  name: "Portugus",
  flag: ""
}];
export const LanguageSwitcher = () => {
  const {
    i18n
  } = useTranslation();
  const {
    user
  } = useAuth();
  const updateLanguagePreference = useUpdateLanguagePreference();
  const currentLanguage = languages.find(lang => lang.code === i18n.language) || languages[0];
  const changeLanguage = (langCode: string) => {
    i18n.changeLanguage(langCode);
    // Save to localStorage for persistence
    localStorage.setItem("i18nextLng", langCode);
    // Update user's language preference in database if logged in
    if (user) {
      updateLanguagePreference.mutate(langCode);
    }
  };
  return <DropdownMenu>
      <DropdownMenuTrigger asChild>
        <Button variant="ghost" size="icon" title="Change Language" className="border-0 text-black hover:bg-black hover:text-white">
          <Globe className="w-5 h-5" />
        </Button>
      </DropdownMenuTrigger>
      <DropdownMenuContent align="end">
        {languages.map(lang => <DropdownMenuItem key={lang.code} onClick={() => changeLanguage(lang.code)} className={`cursor-pointer ${currentLanguage.code === lang.code ? "bg-accent" : ""}`}>
            <span className="mr-2">{lang.flag}</span>
            <span>{lang.name}</span>
          </DropdownMenuItem>)}
      </DropdownMenuContent>
    </DropdownMenu>;
};