import { useQuery, useMutation, useQueryClient } from "@tanstack/react-query";
import { useEffect } from "react";
import { supabase } from "@/integrations/supabase/client";
import { useAuth } from "@/contexts/AuthContext";
import { useToast } from "@/hooks/use-toast";

export const useProfile = () => {
  const { user } = useAuth();
  const queryClient = useQueryClient();
  const userId = user?.id;

  // Set up realtime subscription for profile updates (especially points)
  useEffect(() => {
    if (!userId) return;

    const channel = supabase
      .channel(`profile-updates-${userId}`)
      .on(
        'postgres_changes',
        {
          event: 'UPDATE',
          schema: 'public',
          table: 'profiles',
          filter: `id=eq.${userId}`,
        },
        (payload) => {
          console.log('Profile updated in realtime:', payload);
          // Invalidate the profile query to refresh data
          queryClient.invalidateQueries({ queryKey: ["profile", userId] });
          queryClient.invalidateQueries({ queryKey: ["user-points", userId] });
        }
      )
      .subscribe();

    return () => {
      supabase.removeChannel(channel);
    };
  }, [userId, queryClient]);

  return useQuery({
    queryKey: ["profile", userId],
    queryFn: async () => {
      if (!userId) return null;

      const { data, error } = await supabase
        .from("profiles")
        .select("*")
        .eq("id", userId)
        .single();

      if (error) throw error;
      return data;
    },
    enabled: !!userId,
    staleTime: 0, // Always refetch on mount to get latest data (e.g., points)
    refetchOnMount: "always", // Force refetch when component mounts
  });
};

export const useUpdateProfile = () => {
  const { user } = useAuth();
  const { toast } = useToast();
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: async ({
      display_name,
      phone,
      avatar_url,
      language_preference,
    }: {
      display_name?: string;
      phone?: string;
      avatar_url?: string;
      language_preference?: string;
    }) => {
      if (!user) throw new Error("User not authenticated");

      // Only include defined fields in the update
      const updateData: Record<string, string> = {};
      if (display_name !== undefined) updateData.display_name = display_name;
      if (phone !== undefined) updateData.phone = phone;
      if (avatar_url !== undefined) updateData.avatar_url = avatar_url;
      if (language_preference !== undefined) updateData.language_preference = language_preference;

      const { data, error } = await supabase
        .from("profiles")
        .update(updateData)
        .eq("id", user.id)
        .select()
        .single();

      if (error) throw error;
      return data;
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["profile"] });
      toast({
        title: "Success",
        description: "Profile updated successfully",
      });
    },
    onError: () => {
      toast({
        title: "Error",
        description: "Failed to update profile",
        variant: "destructive",
      });
    },
  });
};

export const useUpdateLanguagePreference = () => {
  const { user } = useAuth();
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: async (language: string) => {
      if (!user) return;

      const { error } = await supabase
        .from("profiles")
        .update({ language_preference: language })
        .eq("id", user.id);

      if (error) throw error;
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["profile"] });
    },
  });
};

export const useUploadAvatar = () => {
  const { user } = useAuth();
  const { toast } = useToast();

  return useMutation({
    mutationFn: async (file: File) => {
      if (!user) throw new Error("User not authenticated");

      const fileExt = file.name.split(".").pop();
      const fileName = `${Date.now()}.${fileExt}`;
      const filePath = `${user.id}/${fileName}`;

      const { error: uploadError } = await supabase.storage
        .from("avatars")
        .upload(filePath, file, { upsert: true });

      if (uploadError) throw uploadError;

      const { data: { publicUrl } } = supabase.storage
        .from("avatars")
        .getPublicUrl(filePath);

      return publicUrl;
    },
    onError: () => {
      toast({
        title: "Error",
        description: "Failed to upload avatar",
        variant: "destructive",
      });
    },
  });
};

export const useUpdateActivityTermsAgreement = () => {
  const { user } = useAuth();
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: async ({ agreed, isSupervisor }: { agreed: boolean; isSupervisor?: boolean }) => {
      if (!user) throw new Error("User not authenticated");

      // If revoking terms, remove all managed restaurants from activities
      if (!agreed) {
        // Get all restaurants managed by this user
        const { data: managedRestaurants, error: fetchError } = await supabase
          .from("merchant_restaurants")
          .select("restaurant_id");

        if (fetchError) throw fetchError;

        if (managedRestaurants && managedRestaurants.length > 0) {
          const restaurantIds = managedRestaurants.map(mr => mr.restaurant_id);
          
          // Remove all these restaurants from all activities
          const { error: deleteError } = await supabase
            .from("activity_restaurants")
            .delete()
            .in("restaurant_id", restaurantIds);

          if (deleteError) throw deleteError;
        }
      }

      const updateData: Record<string, any> = {
        activity_terms_agreed: agreed,
        activity_terms_agreed_at: agreed ? new Date().toISOString() : null,
      };

      const { error } = await supabase
        .from("profiles")
        .update(updateData)
        .eq("id", user.id);

      if (error) throw error;
      return agreed;
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["profile"] });
      queryClient.invalidateQueries({ queryKey: ["merchantParticipatingActivitiesCount"] });
      queryClient.invalidateQueries({ queryKey: ["myParticipatingRestaurants"] });
      queryClient.invalidateQueries({ queryKey: ["activityRestaurantCounts"] });
    },
  });
};
