      // Admin User Manual
      "admin.manual.title": "Manual do Usurio",
      "admin.manual.lastUpdated": "ltima Atualizao",
      "admin.manual.adminDescription": "Guia do administrador para ajud-lo a dominar rapidamente todas as funcionalidades",
      "admin.manual.merchantDescription": "Guia do comerciante/supervisor para ajud-lo a gerenciar restaurantes e atividades de forma eficiente",
      "admin.manual.adminRole": "Administrador",
      "admin.manual.merchantRole": "Comerciante/Supervisor",
      "admin.manual.overview.title": "Viso Geral do Painel",
      "admin.manual.overview.q1": "Como visualizar o status geral do sistema?",
      "admin.manual.overview.a1": "A pgina do painel exibe mtricas principais como total de usurios, restaurantes, atividades e atividades recentes do sistema.",
      "admin.manual.overview.q2": "Como alternar a exibio da barra lateral?",
      "admin.manual.overview.a2": "Clique no boto de recolher na parte inferior da barra lateral para alternar entre os estados expandido e recolhido.",
      "admin.manual.users.title": "Gesto de Usurios",
      "admin.manual.users.q1": "Como criar um novo usurio?",
      "admin.manual.users.a1": "Clique no boto \"Criar Usurio\" na pgina de gesto de usurios, preencha as informaes (email, senha, funo, etc.) para criar um novo usurio. Suporta criao de usurios regulares, comerciantes e supervisores.",
      "admin.manual.users.q2": "Como modificar funes de usurio?",
      "admin.manual.users.a2": "Clique em um usurio na lista para ver detalhes, onde voc pode modificar suas permisses de funo. Nota: Cada usurio pode ter apenas uma funo.",
      "admin.manual.users.q3": "Como atribuir restaurantes aos comerciantes?",
      "admin.manual.users.a3": "Na pgina de detalhes do usurio, clique no boto \"Gerenciar Restaurantes\" para selecionar restaurantes para atribuir. Comerciantes podem ter apenas um restaurante, supervisores podem ter vrios.",
      "admin.manual.users.q4": "Como desativar uma conta de usurio?",
      "admin.manual.users.a4": "Na pgina de detalhes do usurio, clique no boto \"Desativar Conta\" e fornea um motivo. Usurios desativados no podem fazer login no sistema.",
      "admin.manual.restaurants.title": "Gesto de Restaurantes",
      "admin.manual.restaurants.q1": "Como adicionar um novo restaurante?",
      "admin.manual.restaurants.a1": "Clique em \"Adicionar Restaurante\" na pgina de gesto, preencha informaes bsicas, endereo, horrios, carregue imagens e salve.",
      "admin.manual.restaurants.q2": "Como definir visibilidade do restaurante?",
      "admin.manual.restaurants.a2": "Alterne o interruptor \"Mostrar/Ocultar\" na pgina de edio do restaurante para controlar a visibilidade no frontend.",
      "admin.manual.restaurants.q3": "Como gerenciar fotos dos pratos?",
      "admin.manual.restaurants.a3": "Na pgina de detalhes do restaurante na seo \"Fotos dos Pratos\", voc pode carregar, reordenar e excluir imagens de exibio.",
      "admin.manual.activities.title": "Gesto de Atividades",
      "admin.manual.activities.q1": "Como criar uma nova atividade?",
      "admin.manual.activities.a1": "Clique em \"Criar Atividade\" na pgina de gesto, preencha ttulo, contedo, datas, tipo de servio, etc. Voc pode criar como rascunho primeiro para pr-visualizao.",
      "admin.manual.activities.q2": "Qual a diferena entre rascunho e publicado?",
      "admin.manual.activities.a2": "Atividades em rascunho so visveis apenas para administradores e no aparecem no frontend. As atividades ficam visveis para todos aps a publicao.",
      "admin.manual.activities.q3": "Como definir o modo de repetio da atividade?",
      "admin.manual.activities.a3": "As atividades suportam vrios modos de repetio: nenhum, dirio, semanal (dia especfico), mensal, anual, N dias antes do fim. Escolha com base nas caractersticas da atividade.",
      "admin.manual.activities.q4": "Como gerenciar restaurantes participantes?",
      "admin.manual.activities.a4": "Selecione restaurantes participantes na pgina de edio da atividade. Comerciantes e supervisores tambm podem entrar ou sair de atividades na pgina de detalhes.",
      "admin.manual.coupons.title": "Gesto de Cupes",
      "admin.manual.coupons.q1": "Como criar um modelo de cupo?",
      "admin.manual.coupons.a1": "Clique em \"Criar Modelo\" na pgina de gesto de cupes, defina tipo de desconto (percentagem ou fixo), valor, perodo de validade, etc.",
      "admin.manual.coupons.q2": "Como so gerados os cdigos de cupo?",
      "admin.manual.coupons.a2": "Os cdigos de cupo so gerados automaticamente quando os usurios os resgatam. Cada usurio recebe um cdigo de cupo nico.",
      "admin.manual.coupons.q3": "Como verificar o uso de cupes?",
      "admin.manual.coupons.a3": "Veja todos os registros de resgate e uso de cdigos de cupo na pgina de detalhes do modelo.",
      "admin.manual.statistics.title": "Estatsticas e Anlises",
      "admin.manual.statistics.q1": "Como visualizar dados de trfego do site?",
      "admin.manual.statistics.a1": "A pgina de anlises mostra visualizaes de pgina, fontes de visitantes, distribuio de dispositivos, etc. com filtragem por perodo.",
      "admin.manual.statistics.q2": "Como visualizar estatsticas de atividades?",
      "admin.manual.statistics.a2": "A pgina de estatsticas de atividades mostra visualizaes, favoritos, compartilhamentos e grficos de tendncia para cada atividade.",
      "admin.manual.statistics.q3": "Como visualizar dados de pedidos?",
      "admin.manual.statistics.a3": "A pgina de estatsticas de pedidos mostra pedidos e reservas do sistema POS externo, incluindo tendncias e registros detalhados.",
      "admin.manual.security.title": "Gesto de Segurana",
      "admin.manual.security.q1": "Como lidar com denncias de usurios?",
      "admin.manual.security.a1": "Veja todas as denncias na pgina de relatrios. Voc pode marc-las como \"Processando\", \"Resolvido\" ou \"Descartado\" e adicionar notas.",
      "admin.manual.security.q2": "Como gerenciar palavras sensveis?",
      "admin.manual.security.a2": "Adicione ou exclua palavras sensveis na pgina de palavras sensveis. O sistema filtra automaticamente contedo contendo palavras sensveis.",
      "admin.manual.appearance.title": "Configuraes de Aparncia",
      "admin.manual.appearance.q1": "Como modificar o carrossel da pgina inicial?",
      "admin.manual.appearance.a1": "Adicione, edite e reordene contedo do carrossel na pgina de modelos da pgina inicial. Suporta fundos de imagem e vdeo com ttulos, descries e links de botes.",
      "admin.manual.appearance.q2": "Como mudar o logo do site?",
      "admin.manual.appearance.a2": "Carregue um novo logo nas configuraes do site e ajuste suas dimenses. O logo  aplicado automaticamente  navegao e favicon.",
      "admin.manual.notifications.title": "Gesto de Anncios",
      "admin.manual.notifications.q1": "Como enviar notificaes no app?",
      "admin.manual.notifications.a1": "Crie modelos de notificao na pgina de notificaes, ento selecione usurios alvo (todos, por funo ou especficos) para enviar.",
      "admin.manual.notifications.q2": "Como enviar emails em massa?",
      "admin.manual.notifications.a2": "Crie modelos de email com texto rico na pgina de emails, ento selecione grupos de destinatrios para enviar.",
      "admin.manual.merchant.overview.title": "Viso Geral do Painel",
      "admin.manual.merchant.overview.q1": "Quais funcionalidades tm comerciantes/supervisores?",
      "admin.manual.merchant.overview.a1": "Comerciantes e supervisores podem gerenciar informaes do restaurante, participar de atividades da plataforma, consultar uso de cupes e aplicar para criar novas atividades.",
      "admin.manual.merchant.overview.q2": "Como mudar para o painel do comerciante?",
      "admin.manual.merchant.overview.a2": "Aps fazer login, clique no seu avatar no canto superior direito e selecione \"Painel do Comerciante\" ou \"Painel Admin\" para entrar na interface de gesto.",
      "admin.manual.merchant.restaurants.title": "Gesto de Restaurantes",
      "admin.manual.merchant.restaurants.q1": "Como editar informaes do restaurante?",
      "admin.manual.merchant.restaurants.a1": "Encontre seu restaurante na pgina de gesto, clique em editar para modificar nome, endereo, horrios, descrio, etc.",
      "admin.manual.merchant.restaurants.q2": "Qual a diferena entre permisses de comerciante e supervisor?",
